    monospace;
}

:root {
  --text: #040b52;
  --btn-color: #0b1dd5;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

html {
  font-size: 62.5%;
  max-width: 100vw;
}

/* SCROLLBAR STYLING */

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #ebebeb;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(4, 11, 82, 0.6);
}

.navbar {
  box-shadow: 0px 2px 3px 0px hsla(0, 0%, 0%, 0.02);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1;
}

.navbar-container {
  display: -webkit-flex;
  display: flex;
  padding: 2.4rem 16rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.lis {
  display: -webkit-flex;
  display: flex;
  margin-left: auto;
  list-style: none;
}

.link {
  text-decoration: none;
  font-size: 1.6rem;
  color: var(--text);
  opacity: 0.8;
  font-weight: 400;
}

.links {
  margin-right: 4rem;
}

.home {
  color: var(--btn-color);
}

.get-started {
  border: 1px solid var(--btn-color);
  border-radius: 4px;
  padding: 1.1rem 1.8rem;
  font-weight: 600;
  color: var(--btn-color);
}

.nav-fourth {
  transition: 0.3s;
}

.nav-fourth:hover {
  color: #fff;
  background-color: var(--btn-color);
}

.btn-mobile {
  display: none;
  border: none;
  background-color: #fff;
  color: var(--text);
  cursor: pointer;
}

/* MEDIA QUERIES (max-width - 1100px, 990px,780px, 480px) */

@media screen and (max-width: 1100px) {
  .navbar-container {
    padding: 2.4rem 12.7%;
  }
}

@media screen and (max-width: 990px) {
  .navbar-container {
    padding: 2.4rem 8.1%;
  }

  .links {
    margin-right: 3rem;
  }
}

@media screen and (max-width: 780px) {
