    box-shadow: none;
    background-color: #fafbff;
    padding-bottom: 2.4rem;
  }

  .navbar-logo {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    top: 2.4rem;
    left: 10.3%;
  }

  .links {
    margin-right: 0rem;
  }

  .lis {
    display: none;
  }

  .nav-lis-mobile {
    position: absolute;
    height: 20rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    list-style: none;
    top: 7rem;
    transition: all 0.5s ease-out;
    width: 100%;
    background-color: #fafbff;
    z-index: 1;
    padding-bottom: 3.6rem;
  }

  .nav-fourth {
    padding: 1rem 1.2rem;
    -webkit-align-self: center;
            align-self: center;
  }

  .home {
    margin-top: 0;
  }

  .btn-mobile {
    display: block;
